.top-menu {
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding-left: 10px;
    align-items: flex-start;

}
.identification-section {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    margin-bottom: 20px;
}

.detail-section {
    padding: 10px 0px 10px 0px;
}

.photo-section {
    width: 60px;
    height: 80px;
    border-radius: 50%;
    background-image: url('../images/photo.jpg');
    background-position: center;
    background-size: cover;
}

.drawer-button {
    top: 0px;
    left: 1rem;
    position: fixed;
    padding: 10px;
}

.bottom-menu {
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding-left: 10px;
    align-items: flex-start;
    padding-top: 10px;
}

.icon-menu {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    left: 1rem;
    position: fixed;
}

.sideicons {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: 60px;
}
.divide {
    height: 15px;
}

.subdivide {
    height: 5px;
}

.logo-divider {
    height: 4px;
    background-color: #6f8587;
}