.header-section {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100vw;
    height: 50px;
}
.logo-section {
    display: flex;
    flex: 1;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}
.image-section {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-image: url('../images/logo.png');
    background-position: center;
    background-size: cover;
}
.signout-section {
    display: flex;
    flex: 1;
    color: white;
    justify-content: flex-end;
    align-items: center;
}
.signout-button {
    color: white !important;
    margin-right: 10px;
}