.homepage {
    display:flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    overflow: auto;
    height: 100vh;
    width: 100%;
    /*border-radius: 50px;*/
    /* background-image: url("../images/homepage_background.jpg");
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover; */
    padding: 10px 0px 10px 0px;
}
.container {
    display:flex;
    border-radius: 20px;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: 280px;
    height: 380px;
    padding: 0px;
    margin: 5px 0px 5px 0px;
}
.headingsection {
    display: flex;
    background-color: #48AFF0;
    color: white;
    justify-content: center;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    width: 100% ;
    height: 50px;
    align-items: center;
}
.imagesection1 {
    width: 100%;
    height: 200px;
    background-image: url('../images/make_payment1.jpg');
    background-position: center;
    background-size: cover;
}
.imagesection2 {
    width: 100%;
    height: 200px;
    background-image: url('../images/view_accounts1.jpg');
    background-position: center;
    background-size: cover;
}
.detailsection {
    width: 100%;
    height: 100px;
    padding: 10px;
}


